	show = (e) => {
		this.count = 0;
		for (var i = 0; i < this.refs.question.children.length; ++i) {
			if (this.refs.question.children[i].className.indexOf('active') >= 0) {
				this.refs.question.children[i].classList.remove('active');
			}
		}
		if (this.idArr.includes(e.target.id)) {
			e.target.classList.add('active');
		}

		if (e.target.id.indexOf("f") !== -1) {
			var value = e.target.innerHTML;
			this.takeArr[1] = value;

		}
		this.pro = this.takeArr[0];
		if (e.target.id.indexOf("f") === -1) {
			this.takeArr[0] = e.target.id;
		}

		var x = parseInt(this.takeArr[0].split("")[0]);
		var y = parseInt(this.takeArr[0].split("")[1]);
		if (this.takeArr[0] === "") {
			Toast.info("",1,null,false);
			this.takeArr[1] = ""
			return;
		}
		this.setState((state) => {
			if (this.idArr.includes(this.takeArr[0])) {
				state.content[this.state.num-1].question[x][y] = this.takeArr[1];
			}
			this.takeArr[1] = '';
			return {
				content: state.content
			}
		})
		// console.log(this.state.content,this.content)
	}
	minus=(e)=>{
		this.setState((state)=>{
				if(--state.num<1){
						return{
								num:1,
								scsubmitFront:'jlsubmit-front'
						}
				};
				return{
						num:state.num,
						next:""
				}
		})
}
	adds=(e)=>{
		this.setState((state)=>{
			if(state.num===10){
				this.submit()
			}
				if(++state.num>=10){
						return{
								num:10,
								next:""
						}
				};
				return{
						num:state.num,
						next:"",
						scsubmitFront:'jlsubmit-front1'
				}
		})    
}
	change = (e) => {
		e.target.className = (e.target.className === "iconfont icon-xingxing black") ? "iconfont icon-xingxing1 yello" : "iconfont icon-xingxing black"
	}
	submit=(e)=>{
		for(var i=0;i<this.state.content.length;i++){
			this.data[i].answer=this.state.content[i].answer;
			this.data[i].question=this.state.content[i].question;
			this.data[i].status=true;
			for(var m=0;m<this.data[i].answer.length;m++){
				for(var n=0;n<this.data[i].answer[m].length;n++){
					// console.log("m:"+m+" n:"+n+" ="+this.data[0].answer[m][n])
					if(this.data[i].answer[m][n]!==this.data[i].question[m][n]){
						this.data[i].status=false;
					}
				}
			}
		}
		this.data.forEach(item=>{
			if(item.status===true){
				this.score++;
			}
		})

		if (this.$store.getState().token.uid !== '' && this.$store.getState().token.token !== '' && this.$store.getState().users.phone !== '') {
        	Toast.loading('...', 10, () => {
                Toast.offline('', 1, null, false);
			});
			
            this.$api.post_cyjl({data:this.data,score:this.score}).then(res => {
                Toast.hide();
                if (res.data.status === 0) {
                    this.props.history.push('/chengyu/jielong/grade/' + res.data.data);
                } else {
                    Toast.offline('', 1, null, false);
                }
            })
        } else {
            Toast.info('', 1, null, false);
			this.props.history.push({pathname:'/chengyu/jielong/grade',state:{data:this.data,score:this.score}});
        }



		
	}
