import React, { Component } from 'react'
import { NavBar } from 'antd-mobile';
import {Link} from 'react-router-dom';
import '../../css/PinYin/pinyin.css';

export default class Pinyin extends Component {
    render() {
        return (
            <div className="p1">
                <NavBar style={{backgroundColor:"#617ca6",color:"#fff"}}> </NavBar>
                <div className="p2 p7">
                    <div className="p3 p5">
                        <Link to="/pinyin/xue/shengmu"><button className="p4"></button></Link>
                    </div>
                </div>
                <div className="p2">
                    <div className="p3 p6">
                        <Link to="/pinyin/kanzi"><button className="p4"></button></Link>
                    </div>
                </div>
                <div className="p2">
                    <div className="p3 p5">
                        <Link to="/pinyin/xue/yunmu"><button className="p4"></button></Link>
                    </div>
                </div>
                <div className="p2">
                    <div className="p3 p6">
                        <Link to="/pinyin/tingyin"><button className="p4"></button></Link>
                    </div>
                </div>
                <div className="p2">
                    <div className="p3 p5">
                        <Link to="/pinyin/xue/zhengtiyin"><button className="p4"></button></Link>
                    </div>
                </div>
            </div>
        )
    }
}
