import '../../css/Login/login.css';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { NavBar, Button, Icon, Toast } from 'antd-mobile';
import { setTokenAll } from '../../redux/actions';

export default class Login extends Component {
    constructor() {
        super();
        this.state = {
            phone: '',
            password: ''
        }
    }
    changePhone = (e) => {
        var reg = /^[0-9]*$/;
        if (reg.test(e.target.value) && e.target.value.length <= 11) {
            this.setState({
                phone: e.target.value
            })
        }
    }
    changePassword = (e) => {
        if (e.target.value.length <= 20) {
            this.setState({
                password: e.target.value
            })
        }
    }
    login = () => {
        if (this.state.phone.length !== 11) {
            Toast.info('', 1, null, false);
        } else if(this.state.password === '') {
            Toast.info('', 1, null, false);
        }else{
            Toast.loading('...', 10, () => {
                Toast.offline('', 1, null, false);
            });
            let formData = {
                phone: this.state.phone,
                password: this.state.password
            }
            this.$api.login(formData).then(res => {
                // console.log(res);
                Toast.hide();
                if (res.data.status === 0) {
                    Toast.success('', 1, null, false);
                    this.$store.dispatch(setTokenAll(res.data.data.token, res.data.data.uid));
                    // Toast.hide();
                    this.props.history.push('/home/pinyin');
                } else if (res.data.status === -1) {
                    Toast.fail('', 1, null, false);
                } else {
                    Toast.fail('', 1, null, false);
                }
            })
        }
    }
    render() {
        return (
            <div className="logincontainer">
                <div className="wode_back"></div>
                <NavBar mode="dark" icon={<Icon type="left" onClick={()=>{this.props.history.push('/home/wode')}}/>} style={{background:'#617ca6',color:'#fff'}} rightContent={<Link to="/register" style={{color:'#fff'}}></Link>}> </NavBar>
                <div className="logininput">
                    <input type="text" placeholder="" value={this.state.phone} onChange={this.changePhone}/>
                    <input type="password" placeholder="" value={this.state.password} onChange={this.changePassword}/>
                    <div className="methods">
                        <Link to="/login/phone"></Link>
                    </div>
                    <Button 
                        style={{width:'60%',height:'3rem',fontSize:'16px',background:'#617ca6',color:'#fff',
                                margin:'0 auto',lineHeight:'3rem',marginTop:'5%'}}
                        activeStyle={{background:'grey'}}
                        onClick={this.login}
                    > </Button>
                </div>
                {/* <div className="bottomicon">
                    <i className="iconfont icon-qq"></i>
                    <i className="iconfont icon-weixin1"></i>
                </div> */}
            </div>
        )
    }
}
